.main{
    display: grid;
    position:absolute;
    margin-left: 20px;
    top:149px;
    width: 1000px;
    height: auto;
    background-color: rgb(255, 255, 255);
    grid-template-columns: 1fr 1fr 1fr;
    justify-items: center;
    border-top: 4px solid rgb(243, 212, 108);
    box-shadow: 2px 2px 5px 0px rgb(114, 114, 114);
}
.blockCatg{
    display: grid;
    width:300px;
    justify-items: self-start;
}
.item{
    padding: 4px;
    color:black;
    text-decoration:none;
    border-bottom:1px solid rgb(255, 255, 255);
}
    :hover.item{
        border-bottom:1px solid black;
        font-weight: 500;
    }
