.App {
  display: grid;
  height: 100%; 
  width: 100%;
  text-align: center;
  background-color: #212529;
  grid-template-areas: 
  'header header header header'
  'search search search search'
  'toolbar toolbar toolbar toolbar'
  'contents contents contents contents'
  'footer footer footer footer '
  ;
}
.Content{
  display: grid;
  grid-area: contents;
  justify-self: center;
  max-width: 100vw;
}
