.main{
    display: grid;
    max-width: 1440px;
    background-color: #2d3034;
}
.header{
    font-size: 20px;
    font-weight: 500;
    color: aliceblue;
    margin: 20px 0px 0px 0px;
}
.block_manage{
    
    max-width: 1440px;
    display: grid;
    justify-items: center;
    width:98vw;
    padding-top: 15px;
    margin: 0px 0px 0px 0px;
    color: aliceblue;
}
.manage_text{
    width:80%;
    word-break:normal;
    line-break:strict;
    text-align:left;
    font-size: 16px;
    line-height:1.5;
}