.body{
    display: grid;
    max-width: 1440px;
}

.blockSliderFr{
    background-color: #2d3034;
    max-width: 1440px;
    display: grid;
    justify-items: center;
    width:98vw;
    height: 485px;
    padding-top: 15px;
    margin: 0px 0px 0px 0px;
}
.blockSliderSc{
    background-color: #2d3034;
    max-width: 1440px;
    height: 335px;
    display: grid;
    justify-items: center;
    width:98vw;
    padding-top: 15px;
    margin: 15px 0px 0px 0px;
}
.blockMainImg{
    display: grid;
    
    max-width: 1112px;

}
.mainImg{
    width:100%;
    max-height: 230px;
    object-fit: fill;
}
.block{
    display: flex;
    max-width: 1122px;
    height: 410px;
    background-color: rgb(176, 176, 176);
    margin:10px;
    overflow: auto;
    -ms-overflow-style: none;
    position: relative;
}
.block::-webkit-scrollbar {
    width: 0;
    height: 0;
}
.headerCate{
    display: grid;
    justify-items: start;
    font-weight: 600;
    font-size: 19px;
}
.blockA{
    z-index: 6;
    display: flex;
}
.slider{
    height: 600px;
    width: 94%;
    margin-top: 10px;
}
.blockPost{
    max-width: 1440px;
    height: auto;
    display: grid;
    justify-items: center;
    width:98vw;
    
    margin: 15px 0px 0px 0px;
    grid-template-columns: 2fr 15px 1fr;
}
.post{
    display: grid;
    justify-self: center;
    background-color: #2d3034;
    width: 100%;
}
.news{
    display: grid;
    justify-self: center;
    background-color: #2d3034;
    width: 100%;
}
.text{
    display: grid;
    justify-self: center;
    border-bottom: 1px solid rgb(203, 203, 203);
    margin: 20px 0px 10px 0px;
    width: 90%;
    height: 35px;
    color:rgb(203, 203, 203);
    font-size: 21px;
    font-weight: 500;
}
