.main{
    grid-area: toolbar;
    height: 40px;
    display: grid;
    
}
.head{
    background-color: #212529;
    max-width: 1440px;
    width: 100%;
    display: grid;
    justify-self: center;
    align-items:center;
    grid-template-columns: 90px 110px 130px 90px 180px 180px 230px;
    text-decoration:none;
}

.category{
    justify-self: center;
    font-size: 18px;
    text-decoration:none;
    color: rgb(227, 224, 224);
    padding: 6px 0px 7px 0px;
    border-bottom: 2px solid  #212529;
    transition: 0.4s;
}
:hover.category{
    border-bottom: 2px solid rgb(230, 230, 230);
    
}
.catigimg{
    height: 7px;
    width: 12px;
    padding: 0px 0px 1px 10px;
    list-style-type: none; 
    text-decoration:none;
    color: black;
    border-bottom: 1px solid  #212529;
}
  
.bestseller{
    justify-self: center;
