.main{
    display: grid;
    max-width: 1440px;
    margin-top: 20px;
    justify-self: center;
    height:auto;
    width: auto;
    background-color: #263e52;
    grid-area: footer;
    align-self: end;
}
.head{
    display: grid;
    grid-template-columns: 1fr 1fr 0.5fr 1fr 0.5fr 1fr 1fr;
}
.blockInf{
    width: 200px;
    padding: 20px;
    color: white;
}
.itemHeader{
    font-size: 17px;
    font-weight: 600;
    color: rgb(220, 217, 190);
}
.item{
    padding: 12px;
    border-bottom: 1px solid rgb(71, 137, 185);
}
.itemtext{
    list-style-type: none; 
    text-decoration:none;
    color: rgb(230, 230, 230);
}
    :hover.itemtext{
        color: white;
    }