.main{
    grid-area: header;
    max-height: 50px;
    height: 50px;
    display: grid;
}
.head{
    background-color:  #2c2c2c;
    max-width: 1440px;
    display: grid;
    justify-self: center;
    grid-template-columns: 1fr 3fr 1fr;
    
}
.block{
    display: grid;
    align-self: start;
    color:rgb(214, 213, 213);
    padding-left: 8px;
    list-style-type: none; 
    text-decoration:none;
    grid-template-columns: 20px 1fr;
    font-weight: 400;
    
}
.text{
    display: grid;
    align-self: flex-end;
}
.fieldblock{
    
    border-left: 1.3px solid  #000000;
}
.blockleft{
    display: grid;
    width:  232px;
}
.blockright{
    display: grid;
    grid-template-columns: 140px 140px 140px;
}

.contact{
    height: 48px;
    width: 212px;
    margin-top: 2px;
    margin-left: -8px;
}
.help{
    height: 24px;
    width: 24px;
    list-style-type: none; 
    text-decoration:none;
    margin-top: 11px;
    
}
.wishlist{
    height: 22px;
    width: 22px;
    list-style-type: none; 
    text-decoration:none;
    margin-top: 13px;
    
}
.auth{
    height: 22px;
    width: 22px;
    list-style-type: none; 
    text-decoration:none;
    margin-top: 13px;
}
    :hover.block{
        color: rgb(255, 255, 255);
    }