.main{
    display: grid;
    width: 100%;
    height: 300px;
    grid-template-columns: 40px 1fr 40px;
}
.block_sld{
    display: grid;
    grid-template-columns: 1fr 20px 1fr 20px 1fr;
    width: 75vw;
    max-width: 1353px;
}
.slide{
    width: 100%;
    height: 100%;
    max-height: 300px;
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -ms-background-size: cover;
    -o-background-size: cover;
     background-size: cover;
    transition: 1s;
    
    
}
.btn_left{
    align-self: center;
    position: absolute;
    width: 40px;
    height: 40px;
    opacity: 0.6;
    object-fit:contain;
}
.btn_right{
    position: absolute;
    align-self: center;
    justify-self: end;
    width: 40px;
    height: 40px;
    opacity: 0.6;
    object-fit:contain;
}
:hover.btn_left{
    opacity: 1;
}
:hover.btn_right{
    opacity: 1;
}